
.tab-head{box-sizing:border-box;position:relative;margin:0;padding:0;float:left;list-style:none;padding:6px 20px;color:blue;margin:0 2px;border-top-left-radius:3px;border-top-right-radius:3px;cursor:pointer;bottom:-2px
}
.tab-head:hover{background:#f8f8f8
}
.tab-head.active{position:relative;background:white;border-left:1px solid #aaa;border-top:1px solid #aaa;border-right:1px solid #aaa;border-bottom:1px solid white;color:black;z-index:10;cursor:default
}



