
.mdview{line-height:25px;font-size:0.99em
}
.mdview p{margin:6px 0px
}
.mdview blockquote{box-sizing:border-box;border-left:3px solid #aaa;margin:20px 0;padding:1px 25px
}
.mdview blockquote p{margin:6px 0px;color:#454
}
.mdview hr{border:1px solid #f1f1f1
}
.mdview table{border-collapse:collapse
}
.mdview table th,.mdview table td{padding:6px 13px;border:1px solid #ddd
}
.mdview table tbody>tr:nth-of-type(odd){background-color:#f5f5f5
}
.mdview .before-code,.mdview pre code.javascript::before,.mdview pre code.php::before{position:absolute;top:0;right:0;padding:2px 6px;line-height:20px;font-size:0.9em;background:rgba(250,250,250,0.5);color:black
}
.mdview pre{position:relative;font-family:DejaVu Sans Mono
}
.mdview pre code{padding:10px 16px;line-height:25px;letter-spacing:0.03em
}
.mdview pre code.javascript::before{content:'javascipt'
}
.mdview pre code.php::before{content:'php'
}
.mdview p code{background:#282c34;display:inline-block;padding:0 6px;color:#bbb;font-weight:bold;border-radius:3px;word-spacing:0.2em
}
.mdview ol{padding:0 22px;line-height:30px
}
.mdview ul{padding:0 22px
}
.mdview ul li{list-style-type:disc;line-height:30px
}
.mdview .base.textstyle.uncramped{line-height:25px;font-size:0.8em
}
.mdview .base.textstyle.uncramped.displaystyle{line-height:25px;font-size:0.92em
}
.mdview span.katex-display{margin:2px 0;font-weight:italic
}
span.sm{background:red
}



